.splash-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  flex-direction: column;
  position: relative;
  overflow: hidden;
  opacity: 1;
  transition: opacity 6s ease-in-out;
}

.splash-screen.fade-out {
  opacity: 0; /* Pour faire disparaître le splash screen après le délai */
}

/* Titre avec une animation fade-in */
.splash-title {
  font-size: 40px;
  font-family: 'Poppins' !important;
  font-style: italic !important;
  color: #ff6600;
  font-weight: bold;
  opacity: 0;
  animation: fadeInTitle 6s ease-in-out forwards;
}

@keyframes fadeInTitle {
  20% {
    opacity: 0.3;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

/* Container pour les cercles */
.circles-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Animation des cercles */
.circle {
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
  margin: 10px;
  animation: dissolve 6s ease-in-out infinite;
}

@keyframes dissolve {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(2);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

/* Faire apparaître le titre après l'animation de disparition des cercles */
.splash-title.fade-in {
  opacity: 1;
  animation: fadeInTitle 2s ease-in-out forwards;
}

/* Media Queries pour rendre le design réactif */

/* Sur les petits appareils (téléphones) */
@media (max-width: 480px) {
  .splash-title {
    font-size: 30px; /* Réduire la taille du texte pour les petits écrans */
  }

  .circle {
    width: 40px; /* Réduire la taille des cercles sur les petits écrans */
    height: 40px;
    margin: 5px;
  }
}

/* Sur les tablettes (jusqu'à 768px de largeur) */
@media (max-width: 768px) {
  .splash-title {
    font-size: 35px; /* Ajuster la taille du texte pour les tablettes */
  }

  .circle {
    width: 45px; /* Ajuster la taille des cercles sur les tablettes */
    height: 45px;
  }
}

/* Sur les écrans de bureau (au-dessus de 1024px) */
@media (min-width: 1024px) {
  .splash-title {
    font-size: 50px; /* Ajuster la taille du texte pour les grands écrans */
  }

  .circle {
    width: 50px; /* Taille fixe des cercles sur les grands écrans */
    height: 50px;
  }
}
